header {
    height: 110vh;
    padding-top: 4rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100vh;
    position: relative;
}

/*====================================CTA==========================*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/*====================================HeaderSocials==========================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 10rem;
    bottom: 3rem;

}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*====================================ME==========================*/

.me {
    background: var(--color-primary);
    width: 22rem;
    height: 27.2rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding-top: 5rem 1.5rem 1.5rem 1.5rem;
}

/*====================================Scroll Down==========================*/

.scroll__down {
    position: absolute;
    right: 2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}



/*==================Media Quieries (Medium devices)=====================*/

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}




/*==================Media Quieries (small devices)=====================*/

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials{
        display: none;
       
    }
    .scroll__down {
        
        display: none;
        
    }
}
